.flats-slider-wrapper {
    // margin-top: 130px
    position: relative; }
.flats-slider {
    // padding-bottom: 30%
    width: 100%; }

.flats-slide {
    &__image {
        @include cover;
        // height: 100%
        // padding-bottom: 30%
        padding-bottom: 400px;
 } }        // margin: 0 20px

.flat-info-wrapper {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center; }

.flats-thumbs {
    &__image {
        // width: 150px
        padding-bottom: 100%;
        // margin: 20px
        @include cover;
        border-radius: 10px; } }

// .tns-slide-active
//     box-shadow: 2px 2px 0 2px green
[data-controls="prev"] {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 50px;
    z-index: 19; }

[data-controls="next"] {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    font-size: 50px;
    z-index: 19; }

.tns-outer {
    position: relative; }
.flats-thumbs-wrapper {
    overflow: hidden;
    margin: 0 50px; }
